import Vue from 'vue';
import Router from 'vue-router';
import Main from '@/components/circleOfWork/Main';

Vue.use(Router);

const lazyComponent = {
    post: resolve => require(['@/components/circleOfWork/modules/Post'], resolve)
};

export default new Router({
    routes: [
        {
            path: "/circle",
            name: "circle",
            component: Main,
            children: [
                {
                    path: "post/:postId",
                    name: "post",
                    component: lazyComponent.post
                }
            ]
        },
        {
            path: "*",
            redirect: "/circle/post/12"
        }
    ]
})



